<template>
  <div class="class-view">
    <div class="teacher-class-header">
      <span class="title">我的班级—— 三年(2)班概览</span>
      <span class="back-btn" @click="back">返回</span>
    </div>
    <div class="class-detail-line">
      <div class="detail-item">
        <div class="space-detail-header">
          <span class="title">班级总人数</span>
          <span class="to-detail" @click="toDetail('num')">查看详情</span>
        </div>
        <div class="total-num">{{studentsTotal}}</div>
        <div class="catalog-num">
          <div class="number">男生：{{boys}}</div>
          <div class="line"></div>
          <div class="number">女生：{{girls}}</div>
        </div>
      </div>
      <div class="detail-item">
        <div class="space-detail-header">
          <span class="title">学生签到</span>
          <span class="to-detail" @click="toDetail('signIn')">查看详情</span>
        </div>
        <div id="signInChart" style="width: 100%;height:90%"></div>
      </div>
      <div class="detail-item">
        <div class="space-detail-header">
          <span class="title">班级通知</span>
          <span class="to-detail" @click="toDetail('notice')">查看详情</span>
        </div>
        <div class="notice-box">
          <div class="title">校运会通知</div>
          <div class="content">&nbsp;&nbsp;根据学校安排，将于9月21日举行为期3天的校运会。期间请同学们注意班级纪律，不要乱跑，待在本班的集合地点！</div>
        </div>
      </div>
    </div>
    <div class="class-task">
      <div class="left">
        <div class="space-detail-header">
          <span class="title">课堂活动</span>
          <span class="to-detail" @click="toDetail('activity')">查看详情</span>
        </div>
        <div class="activity-box">
          <div class="activity-item" v-for="(item,index) in activities" :key="index">
            <div class="icon blue-bg">{{item.key}}</div>
            <div class="detail">
              <div class="top">
                <span class="title">{{item.title}}</span>
                <span
                  class="status"
                  :class="{'red-bg':item.status=='已结束','green-bg':item.status=='进行中','blue-bg':item.status=='未开始'}"
                >{{item.status}}</span>
              </div>
              <div class="bottom">
                <span>共{{item.questions}}道题目</span>
                <span>共{{item.did}}人作答</span>
                <span>{{item.time}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="course-page" style="margin-top:28px">
          <el-pagination layout="prev, pager, next" :page-size="5" :total="allItems"></el-pagination>
        </div>
      </div>
      <div class="right">
        <div class="score">
          <div class="space-detail-header">
            <span class="title">成绩</span>
            <span class="to-detail" @click="toDetail('score')">查看详情</span>
          </div>
          <div id="scoreChart" style="width:100%;height: 88%"></div>
        </div>
        <div class="home-work">
          <div class="space-detail-header">
            <span class="title">作业及任务</span>
            <span class="to-detail" @click="toDetail('task')">查看详情</span>
          </div>
          <div class="home-work-box">
            <div class="home-work-item" v-for="(item,index) in taskList" :key="index">
              <div class="title">{{item.title}}</div>
              <div class="detail">
                <span class="class">布置班级：{{item.class}}</span>
                <span class="time">日期：{{item.time}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "echarts/lib/chart/bar";
import "echarts/lib/chart/pie";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
let echarts = require("echarts/lib/echarts");
export default {
  data() {
    return {
      allItems: 20,
      studentsTotal: 52,
      boys: 22,
      girls: 30,
      activities: [
        {
          key: "投",
          title: "谁是三年二班最帅的人",
          status: "进行中",
          questions: 15,
          did: 1,
          time: "2018-11-01"
        },
        {
          key: "投",
          title: "谁是三年二班最帅的人",
          status: "未开始",
          questions: 15,
          did: 1,
          time: "2018-11-01"
        },
        {
          key: "投",
          title: "谁是三年二班最帅的人",
          status: "未开始",
          questions: 15,
          did: 1,
          time: "2018-11-01"
        },
        {
          key: "投",
          title: "谁是三年二班最帅的人",
          status: "已结束",
          questions: 15,
          did: 1,
          time: "2018-11-01"
        },
        {
          key: "投",
          title: "谁是三年二班最帅的人",
          status: "已结束",
          questions: 15,
          did: 1,
          time: "2018-11-01"
        }
      ],
      taskList: [
        {
          title: "朗读并背诵全文《闰土与猹》",
          class: "三年(2班)",
          time: "2018-11-01"
        },
        {
          title: "朗读并背诵全文《闰土与猹》",
          class: "三年(2班)",
          time: "2018-11-01"
        },
        {
          title: "朗读并背诵全文《闰土与猹》",
          class: "三年(2班)",
          time: "2018-11-01"
        }
      ]
    };
  },
  mounted() {
    this.drawSignInChart();
    this.drawScoreChart();
  },
  methods: {
    back() {
      this.$router.back();
    },
    drawSignInChart() {
      let option = {
        xAxis: {
          type: "category",
          data: ["迟到", "早退", "旷课"],
          axisLabel: {
            color: "#2c3e50"
          },
          axisLine: {
            lineStyle: {
              color: "#aaa"
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          type: "value",
          axisLabel: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#aaa"
            }
          }
        },
        grid: {
          top: "18%",
          bottom: "15%",
          left: "2%",
          right: "2%"
        },
        series: [
          {
            label: {
              show: true,
              position: "top",
              color: "#2c3e50",
              fontSize: 12
            },
            data: [
              {
                value: 5,
                itemStyle: {
                  color: "#EEB00A"
                }
              },
              {
                value: 3,
                itemStyle: {
                  color: "#4CD64C"
                }
              },
              {
                value: 1,
                itemStyle: {
                  color: "#F77070"
                }
              }
            ],
            type: "bar",
            barWidth: 22
          }
        ]
      };
      let chart = echarts.init(document.getElementById("signInChart"));
      chart.setOption(option);
    },
    drawScoreChart() {
      let datas = [8, 36];
      let option = {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          bottom: "bottom",
          formatter: function(name) {
            return (
              `${name}：${name == "不及格" ? datas[0] : datas[1]}人`
            );
          },
          data: ["及格", "不及格"]
        },
        color: ["#EEB00A", "#3FABFF"],
        series: [
          {
            name: "三年(2)班",
            type: "pie",
            radius: "69%",
            center: ["50%", "45%"],
            label: {
              show: false
            },
            data: [
              { value: datas[0], name: "不及格" },
              { value: datas[1], name: "及格" }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      let chart = echarts.init(document.getElementById("scoreChart"));
      chart.setOption(option);
    },
    toDetail(cmd = "") {
      switch (cmd) {
        case "num":
          //班级人数
          this.$router.push("/main/teacherSpace/classMana/students/123");
          break;

        case "signIn":
          //签到
          this.$router.push("/main/teacherSpace/classMana/signIn/123");
          break;

        case "notice":
          //通知
          break;

        case "activity":
          //活动
          break;

        case "score":
          //成绩
          this.$router.push("/main/teacherSpace/classMana/score/123");
          break;
        case "task":
          //作业及任务
          break;

        default:
          break;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.class-view {
  .space-detail-header {
    width: 100%;
    height: 21px;
    .title {
      float: left;
    }
    .to-detail {
      font-size: 0.6rem;
      border-radius: 12px;
      padding: 2px 8px 3px 8px;
      color: #fff;
      background: #3fabff;
      float: right;
      &:hover {
        cursor: pointer;
      }
    }
  }
  .class-detail-line {
    width: 100%;
    height: 200px;
    margin-top: 22px;
    display: flex;
    justify-content: space-between;
    .detail-item {
      width: 31.5%;
      height: 100%;
      background: #fff;
      padding: 12px 22px;
      .total-num {
        width: 100%;
        height: 40px;
        font-size: 3.2rem;
        margin: 28px 0;
      }
      .catalog-num {
        width: 100%;
        height: 36px;
        margin-top: 42px;
        div {
          float: left;
        }
        .number {
          width: 49%;
          margin-top: 6px;
        }
        .line {
          width: 1px;
          height: 36px;
          background: #888;
        }
      }
      .notice-box {
        width: 100%;
        padding: 0 22px;
        margin-top: 18px;
        .title {
          width: 100%;
        }
        .content {
          width: 100%;
          margin-top: 8px;
          text-align: left;
          font-size: 0.9rem;
        }
      }
    }
  }
  .class-task {
    width: 100%;
    height: 592px;
    margin-top: 22px;
    display: flex;
    justify-content: space-between;
    .left {
      width: 65.8%;
      height: 100%;
      background: #fff;
      padding: 12px 22px;
      .activity-box {
        width: 100%;
        .activity-item {
          width: 100%;
          height: 66px;
          margin-top: 22px;
          border-bottom: 1px solid #aaa;
          &:hover {
            cursor: pointer;
          }
          .icon {
            width: 46px;
            height: 46px;
            float: left;
            border-radius: 3px;
            line-height: 46px;
            color: #fff;
            font-size: 1.5rem;
          }
          .detail {
            width: 70%;
            height: 46px;
            float: left;
            margin-left: 22px;
            .top {
              text-align: left;
              .title {
                margin-right: 12px;
              }
              .status {
                color: #fff;
                font-size: 0.6rem;
                padding: 2px 5px;
                border-radius: 3px;
              }
            }
            .bottom {
              width: 70%;
              margin-top: 3.6px;
              color: #aaa;
              font-size: 0.8rem;
              span {
                width: 33.33%;
                float: left;
                border-left: 1px solid #aaa;
              }
            }
          }
        }
      }
    }
    .right {
      width: 31.5%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      .score,
      .home-work {
        width: 100%;
        height: 48%;
        background: #fff;
        padding: 12px 22px;
        .home-work-box {
          width: 100%;
          padding-top: 22px;
          .home-work-item {
            width: 100%;
            height: 50px;
            margin-bottom: 12px;
            border-bottom: 1px solid #aaa;
            text-align: left;
            &:hover {
              cursor: pointer;
            }
            .detail {
              color: #aaa;
              font-size: 0.8rem;
              .class {
                float: left;
              }
              .time {
                float: right;
              }
            }
          }
        }
      }
    }
  }
}
</style>



